/**
 * deviceApi.ts -    API   ()
 *
 * :
 * - API_BASE  '/api'  (env   )
 * - axios (http)  + Accept  
 * -  text/plain JSON     
 * -   (ensureArray)
 * -  params 
 */

import axios, { AxiosError } from "axios";
import type { Device } from "../types/device";
import type { Port } from "../types/port";
import type { DeviceStatus } from "../types/status";

/** API  URL: env  '/api' */
const API_BASE = (import.meta.env.VITE_API_BASE ?? "/api").replace(/\/$/, "");
const isDev = import.meta.env.DEV;

/**  axios  */
const http = axios.create({
  baseURL: API_BASE,
  timeout: 15000,
  headers: { Accept: "application/json" },
});

/**  JSON   ( text/plain   ) */
http.interceptors.response.use((res) => {
  const d = res.data;
  if (typeof d === "string") {
    const s = d.trim();
    if ((s.startsWith("{") && s.endsWith("}")) || (s.startsWith("[") && s.endsWith("]"))) {
      try { res.data = JSON.parse(s); } catch { /* ignore */ }
    }
  }
  return res;
});

/**    */
function extractErrorMessage(error: unknown): string {
  if (axios.isAxiosError(error)) {
    const ax = error as AxiosError<{ message?: string }>;
    return ax.response?.data?.message ?? ax.message ?? "  ";
  }
  if (error instanceof Error) return error.message;
  return "       .";
}

/**   */
function ensureArray<T>(data: unknown): T[] {
  return Array.isArray(data) ? (data as T[]) : [];
}

/*   API  */

export async function fetchDevices(): Promise<Device[]> {
  try {
    const res = await http.get("/device");
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    if (isDev) console.log(" /device  :", typeof res.data, "len:", (res.data as any)?.length);
    return ensureArray<Device>(res.data);
  } catch (error) {
    console.error("   :", error);
    throw new Error(extractErrorMessage(error));
  }
}

/*   API  */

export async function fetchPortsByDevice(deviceId: number): Promise<Port[]> {
  try {
    const res = await http.get("/port", { params: { deviceId } });
    if (isDev) console.log(` /port?deviceId=${deviceId} :`, typeof res.data);
    return ensureArray<Port>(res.data);
  } catch (error) {
    console.error(`    (deviceId: ${deviceId}):`, error);
    throw new Error(extractErrorMessage(error));
  }
}

export async function fetchAllPorts(): Promise<Port[]> {
  try {
    const res = await http.get("/ports");
    if (isDev) console.log(" /ports :", typeof res.data);
    return ensureArray<Port>(res.data);
  } catch (error) {
    console.error("    :", error);
    throw new Error(extractErrorMessage(error));
  }
}

/*     */

export async function updateDeviceStatus(
  deviceId: number,
  status: DeviceStatus,
  enablePing?: boolean
): Promise<Device> {
  try {
    const res = await http.put(`/device/${deviceId}/status`, { status, enablePing });
    if (isDev) console.log(`   #${deviceId}  ${status}${enablePing !== undefined ? `, ping=${enablePing}` : ""}`, res.data);
    return res.data as Device;
  } catch (error) {
    console.error("   :", error);
    throw new Error(extractErrorMessage(error));
  }
}

export async function updateDeviceStatusBulk(params: {
  deviceIds: number[];
  status: DeviceStatus;
  enablePing?: boolean;
}): Promise<number> {
  try {
    const res = await http.put(`/device/status/bulk`, params);
    if (isDev) console.log(`    ${params.deviceIds.length}  ${params.status}`, res.data);
    return (res.data as number) ?? 0;
  } catch (error) {
    console.error("   :", error);
    throw new Error(extractErrorMessage(error));
  }
}

/*    */

export async function deleteDevice(deviceId: number): Promise<void> {
  await http.delete(`/device/${deviceId}`);
}

export async function deleteCable(cableId: string | number): Promise<void> {
  await http.delete(`/cable/${cableId}`);
}
