/* eslint-disable @typescript-eslint/no-explicit-any */
/**
 * useKeyboardNavigation.ts - React Flow    
 * 
 * :
 * -        
 * -      (, , )
 * -       
 * 
 *   :
 * - :   ()
 * - Ctrl/Cmd + Plus/Minus:  /
 * - Ctrl/Cmd + 0:  
 * 
 * :
 * - (input, textarea)    
 * - React Flow    (window  )
 * -    (0.3 ~ 2.0) 
 */

import { useEffect, useCallback, useMemo } from 'react';
import type { KeyboardNavigationConfig, KeyboardControls } from '../types/keyboard';

/**
 * React Flow   
 * 
 * @param config    
 * @param config.stepSize    (, : 100)
 * @param config.enabled     (: true)
 * @param config.zoomStep   (: 1.2)
 * @param config.animationDuration     (, : 300)
 * @returns     
 * 
 * @example
 * ```tsx
 * //  
 * const controls = useKeyboardNavigation();
 * 
 * //  
 * const controls = useKeyboardNavigation({
 *   stepSize: 50,        //   
 *   zoomStep: 1.1,       //   
 *   animationDuration: 200, //   
 *   enabled: !isModalOpen   //    
 * });
 * 
 * //   
 * <button onClick={controls.zoomIn}> </button>
 * <button onClick={controls.resetView}> </button>
 * ```
 */
export const useKeyboardNavigation = ({
  stepSize = 100,
  enabled = true,
  zoomStep = 1.2,
  animationDuration = 300
}: KeyboardNavigationConfig = {}): KeyboardControls => {

  /**
   *   
   * 
   *  :
   * 1.      (   )
   * 2. React Flow      
   * 3.     
   * 4.     (,  )
   */
  const handleKeyDown = useCallback((event: KeyboardEvent) => {
    if (!enabled) return;

    /**
     *       
     * 
     *        
     *    
     */
    const activeElement = document.activeElement;
    if (activeElement && (
      activeElement.tagName === 'INPUT' || 
      activeElement.tagName === 'TEXTAREA' ||
      activeElement.getAttribute('contenteditable') === 'true'
    )) {
      return;
    }

    /**
     * React Flow   
     * 
     * : window     ,
     * React Flow  API    
     *   Context ref     
     */
    const reactFlowInstance = (window as any).reactFlowInstance;
    if (!reactFlowInstance || !reactFlowInstance.getViewport) return;

    //    
    const viewport = reactFlowInstance.getViewport();
    let newViewport = { ...viewport };
    let shouldUpdate = false;

    /**
     *     
     * 
     *  :
     * - React Flow SVG   (Y  )
     * -     Y   
     */
    switch (event.code) {
      case 'ArrowUp':
        event.preventDefault();
        newViewport.y += stepSize;  //    ( )
        shouldUpdate = true;
        break;
        
      case 'ArrowDown':
        event.preventDefault();
        newViewport.y -= stepSize;  //    ( )
        shouldUpdate = true;
        break;
        
      case 'ArrowLeft':
        event.preventDefault();
        newViewport.x += stepSize;  //    ( )
        shouldUpdate = true;
        break;
        
      case 'ArrowRight':
        event.preventDefault();
        newViewport.x -= stepSize;  //    ( )
        shouldUpdate = true;
        break;
        
      /**
       *   ()
       * Ctrl/Cmd + Plus  Ctrl/Cmd + Numpad Plus
       *    2.0 
       */
      case 'Equal':       // +  (Shift )
      case 'NumpadAdd':   //  + 
        if (event.ctrlKey || event.metaKey) {
          event.preventDefault();
          newViewport.zoom = Math.min(viewport.zoom * zoomStep, 2.0);
          shouldUpdate = true;
        }
        break;
        
      /**
       *   ()
       * Ctrl/Cmd + Minus  Ctrl/Cmd + Numpad Minus
       *    0.3 
       */
      case 'Minus':
      case 'NumpadSubtract':
        if (event.ctrlKey || event.metaKey) {
          event.preventDefault();
          newViewport.zoom = Math.max(viewport.zoom / zoomStep, 0.3);
          shouldUpdate = true;
        }
        break;
        
      /**
       *  
       * Ctrl/Cmd + 0: (0,0)    (0.8) 
       */
      case 'Digit0':
        if (event.ctrlKey || event.metaKey) {
          event.preventDefault();
          newViewport = { x: 0, y: 0, zoom: 0.8 };
          shouldUpdate = true;
        }
        break;
    }

    /**
     *   
     * duration     
     */
    if (shouldUpdate && reactFlowInstance.setViewport) {
      reactFlowInstance.setViewport(newViewport, { duration: animationDuration });
    }
  }, [enabled, stepSize, zoomStep, animationDuration]);

  /**
   *    /
   * 
   * document    
   * React Flow     
   */
  useEffect(() => {
    if (!enabled) return;

    document.addEventListener('keydown', handleKeyDown);

    // :   
    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [handleKeyDown, enabled]);

  /**
   *     
   * 
   *         
   *        
   */
  const controls = useMemo((): KeyboardControls => ({
    /**
     *   
     *  ArrowUp  
     */
    moveUp: () => {
      const reactFlowInstance = (window as any).reactFlowInstance;
      if (reactFlowInstance?.getViewport && reactFlowInstance?.setViewport) {
        const viewport = reactFlowInstance.getViewport();
        reactFlowInstance.setViewport(
          { ...viewport, y: viewport.y + stepSize }, 
          { duration: animationDuration }
        );
      }
    },

    /**
     *   
     *  ArrowDown  
     */
    moveDown: () => {
      const reactFlowInstance = (window as any).reactFlowInstance;
      if (reactFlowInstance?.getViewport && reactFlowInstance?.setViewport) {
        const viewport = reactFlowInstance.getViewport();
        reactFlowInstance.setViewport(
          { ...viewport, y: viewport.y - stepSize }, 
          { duration: animationDuration }
        );
      }
    },

    /**
     *   
     *  ArrowLeft  
     */
    moveLeft: () => {
      const reactFlowInstance = (window as any).reactFlowInstance;
      if (reactFlowInstance?.getViewport && reactFlowInstance?.setViewport) {
        const viewport = reactFlowInstance.getViewport();
        reactFlowInstance.setViewport(
          { ...viewport, x: viewport.x + stepSize }, 
          { duration: animationDuration }
        );
      }
    },

    /**
     *   
     *  ArrowRight  
     */
    moveRight: () => {
      const reactFlowInstance = (window as any).reactFlowInstance;
      if (reactFlowInstance?.getViewport && reactFlowInstance?.setViewport) {
        const viewport = reactFlowInstance.getViewport();
        reactFlowInstance.setViewport(
          { ...viewport, x: viewport.x - stepSize }, 
          { duration: animationDuration }
        );
      }
    },

    /**
     *   ()
     *    2.0  
     */
    zoomIn: () => {
      const reactFlowInstance = (window as any).reactFlowInstance;
      if (reactFlowInstance?.getViewport && reactFlowInstance?.setViewport) {
        const viewport = reactFlowInstance.getViewport();
        reactFlowInstance.setViewport(
          { ...viewport, zoom: Math.min(viewport.zoom * zoomStep, 2.0) }, 
          { duration: animationDuration }
        );
      }
    },

    /**
     *   ()
     *    0.3  
     */
    zoomOut: () => {
      const reactFlowInstance = (window as any).reactFlowInstance;
      if (reactFlowInstance?.getViewport && reactFlowInstance?.setViewport) {
        const viewport = reactFlowInstance.getViewport();
        reactFlowInstance.setViewport(
          { ...viewport, zoom: Math.max(viewport.zoom / zoomStep, 0.3) }, 
          { duration: animationDuration }
        );
      }
    },

    /**
     *  
     *     (0.8) 
     *       
     */
    resetView: () => {
      const reactFlowInstance = (window as any).reactFlowInstance;
      if (reactFlowInstance?.setViewport) {
        reactFlowInstance.setViewport(
          { x: 0, y: 0, zoom: 0.8 }, 
          { duration: animationDuration * 2 }
        );
      }
    },

    /**
     *    
     *   /  
     * 
     * @returns    { x, y, zoom }
     */
    getCurrentViewport: () => {
      const reactFlowInstance = (window as any).reactFlowInstance;
      if (reactFlowInstance?.getViewport) {
        return reactFlowInstance.getViewport();
      }
      // React Flow     
      return { x: 0, y: 0, zoom: 1 };
    }
  }), [stepSize, zoomStep, animationDuration]);

  return controls;
};