/**
 * @fileoverview Network Device Ping API Client
 * @description TraceNet    Ping   API 
 */

import axios from "axios";
import type {
  PingResultDto,
  MultiPingRequestDto,
  TracePingResultDto,
} from "../types/ping";

// API  URL  -     
const API_BASE = (import.meta.env.VITE_API_BASE ?? "/api").replace(/\/$/, "");

/**
 *    Ping  .
 * 
 * @description 
 *   ID      .
 *       .
 * 
 * @param {number} deviceId -    
 * @returns {Promise<PingResultDto>} Ping   (, ,  )
 * 
 * @throws {Error}  ,  ,     
 * 
 * @example
 * ```typescript
 * try {
 *   const result = await pingDevice(123);
 *   console.log(` : ${result.status}, : ${result.latencyMs}ms`);
 * } catch (error) {
 *   console.error('Ping :', error.message);
 * }
 * ```
 */
export async function pingDevice(deviceId: number): Promise<PingResultDto> {
  try {
    console.log(`  Ping : Device ID ${deviceId}`);

    const response = await axios.post<PingResultDto>(
      `${API_BASE}/device/${deviceId}/ping`,
      {
        headers: { "Content-Type": "application/json" },
      }
    );

    console.log(
      `  Ping : ${response.data.deviceName} - ${response.data.status}`
    );
    return response.data;
  } catch (error) {
    console.error(`  Ping  (Device ID: ${deviceId}):`, error);
    throw new Error(`Device ID ${deviceId} Ping .`);
  }
}

/**
 *     Ping  .
 * 
 * @description 
 *     Ping    .
 *       .
 * 
 * @param {MultiPingRequestDto} request -  Ping   ( ID  )
 * @returns {Promise<PingResultDto[]>}   Ping   
 * 
 * @throws {Error}         
 * 
 * @performance 
 * -    Ping : 50
 * -   :   2-5
 * 
 * @example
 * ```typescript
 * const request = { deviceIds: [1, 2, 3, 4, 5] };
 * const results = await pingMultipleDevices(request);
 * const onlineCount = results.filter(r => r.status === 'Online').length;
 * ```
 */
export async function pingMultipleDevices(
  request: MultiPingRequestDto
): Promise<PingResultDto[]> {
  try {
    console.log(`  Ping : ${request.deviceIds.length} `);

    const response = await axios.post<PingResultDto[]>(
      `${API_BASE}/device/ping/multi`,
      request,
      {
        headers: { "Content-Type": "application/json" },
      }
    );

    const results = response.data;
    
    //     
    const online = results.filter((r) => r.status === "Online").length;
    const offline = results.filter((r) => r.status === "Offline").length;
    const unstable = results.filter((r) => r.status === "Unstable").length;

    console.log(
      `  Ping :  ${results.length} - : ${online}, : ${offline}, : ${unstable}`
    );
    return results;
  } catch (error) {
    console.error("  Ping :", error);
    throw new Error("  Ping .");
  }
}

/**
 *       Ping  .
 * 
 * @description 
 *        .
 *         .
 * 
 * @returns {Promise<PingResultDto[]>}   Ping   
 * 
 * @throws {Error}          
 * 
 * @performance 
 * -  : 200   5-8 
 * -  :  50-100KB  
 * - :  2000ms 
 * 
 * @security 
 *       :
 * -    ICMP   
 * -      
 * -  Ping    
 * 
 * @example
 * ```typescript
 * try {
 *   const allResults = await pingAllDevices();
 *   const healthReport = {
 *     total: allResults.length,
 *     online: allResults.filter(r => r.status === 'Online').length,
 *     issues: allResults.filter(r => r.status !== 'Online').length
 *   };
 *   console.log('  :', healthReport);
 * } catch (error) {
 *   console.error(' Ping  :', error.message);
 * }
 * ```
 */
export async function pingAllDevices(): Promise<PingResultDto[]> {
  try {
    console.log("  Ping :   ");

    const response = await axios.post<PingResultDto[]>(
      `${API_BASE}/device/ping/all`,
      {},
      {
        headers: { "Content-Type": "application/json" },
      }
    );

    const results = response.data;
    
    //   
    const online = results.filter((r) => r.status === "Online").length;
    const offline = results.filter((r) => r.status === "Offline").length;
    const unstable = results.filter((r) => r.status === "Unstable").length;
    const unreachable = results.filter(
      (r) => r.status === "Unreachable"
    ).length;

    console.log(
      `  Ping :  ${results.length} - : ${online}, : ${offline}, : ${unstable}, : ${unreachable}`
    );
    return results;
  } catch (error) {
    //     
    if (axios.isAxiosError(error)) {
      if (error.code === "ECONNREFUSED") {
        throw new Error("   .  .");
      }
      if (error.response?.status === 500) {
        throw new Error(
          "   .  ."
        );
      }
    }
    throw new Error("  Ping .");
  }
}

/**
 *   TracePath    Ping.
 * 
 * @description 
 *   (TracePath)        .
 *       .
 * 
 * @param {number} deviceId - Trace     ID
 * @returns {Promise<TracePingResultDto>}    Ping   
 * 
 * @throws {Error}     Ping     
 * 
 * @use_case 
 * -        
 * -      
 * -    
 * 
 * @integration 
 *   TraceController     .
 *  TracePath      .
 * 
 * @example
 * ```typescript
 * //  PC      
 * const traceResult = await pingTracePath(42);
 * console.log(`  ${traceResult.totalDevices}  ${traceResult.onlineDevices} `);
 * 
 * if (traceResult.offlineDevices > 0) {
 *   console.log('   .   .');
 * }
 * ```
 */
export async function pingTracePath(
  deviceId: number
): Promise<TracePingResultDto> {
  try {
    console.log(` TracePath Ping : Device ID ${deviceId}`);

    const response = await axios.get<TracePingResultDto>(
      `${API_BASE}/trace/${deviceId}/ping`
    );

    const result = response.data;
    console.log(
      ` TracePath Ping : ${result.totalDevices}  - : ${result.onlineDevices}, : ${result.offlineDevices}`
    );
    return result;
  } catch (error) {
    console.error(` TracePath Ping  (Device ID: ${deviceId}):`, error);
    throw new Error(`Device ID ${deviceId} TracePath Ping .`);
  }
}

