/**
 * cableApi.ts -   API   ()
 *
 * 
 * - API_BASE  '/api'  (env   )
 * - axios (http)  + Accept  
 * -  text/plain JSON    
 * -  (ensureArray)  
 */

import axios, { AxiosError } from "axios";
import type { CableDto } from "../types/cable";

/** trace    ( ) */
export interface CableConnection {
  cableId: string;
  type: string;
  description: string;
  fromPort: { id: number; name: string; deviceName: string };
  toPort:   { id: number; name: string; deviceName: string };
}

/** API  URL: env  '/api' */
const API_BASE = (import.meta.env.VITE_API_BASE ?? "/api").replace(/\/$/, "");
const isDev = import.meta.env.DEV;

/**  axios  */
const http = axios.create({
  baseURL: API_BASE,
  timeout: 15000,
  headers: { Accept: "application/json" },
});

/**  JSON   ( text/plain  ) */
http.interceptors.response.use((res) => {
  const d = res.data;
  if (typeof d === "string") {
    const s = d.trim();
    if ((s.startsWith("{") && s.endsWith("}")) || (s.startsWith("[") && s.endsWith("]"))) {
      try { res.data = JSON.parse(s); } catch { /* ignore */ }
    }
  }
  return res;
});

/**    */
function extractErrorMessage(error: unknown): string {
  if (axios.isAxiosError(error)) {
    const ax = error as AxiosError<{ message?: string }>;
    return ax.response?.data?.message ?? ax.message ?? "  ";
  }
  if (error instanceof Error) return error.message;
  return "       .";
}

/**   */
function ensureArray<T>(data: unknown): T[] {
  return Array.isArray(data) ? (data as T[]) : [];
}

/*  API   */

/**      : GET /api/trace/cables/{deviceId} */
export async function fetchTraceCables(deviceId: number): Promise<CableConnection[]> {
  try {
    const res = await http.get(`/trace/cables/${deviceId}`);
    if (isDev) console.log(` /trace/cables/${deviceId} :`, typeof res.data);
    return ensureArray<CableConnection>(res.data);
  } catch (error) {
    console.error("   :", error);
    throw new Error(extractErrorMessage(error));
  }
}

/**   : GET /api/cable */
export async function fetchCables(): Promise<CableDto[]> {
  try {
    const res = await http.get(`/cable`);
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    if (isDev) console.log(" /cable :", typeof res.data, "len:", (res.data as any)?.length);
    return ensureArray<CableDto>(res.data);
  } catch (error) {
    console.error("    :", error);
    throw new Error(extractErrorMessage(error));
  }
}
