/**
 * @fileoverview     
 * 
 * React Flow     
 * 
 *  :
 * -  /     
 * -    (, Ctrl+Home )
 * -      overview
 * -        
 * -      
 * 
 *  :
 * - React.memo    
 * - requestAnimationFrame   
 * -     
 * -       
 */

/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable react-hooks/exhaustive-deps */

import React, { useCallback, useRef, useMemo, useEffect } from "react";
import ReactFlow, {
  MiniMap,
  type Node,
  type Edge,
  type NodeTypes,
  type EdgeTypes,
  type ReactFlowInstance,
} from "react-flow-renderer";
import type { Device } from "../types/device";
import { useKeyboardNavigation } from "../hooks/useKeyboardNavigation";

// ==========================================
//  
// ==========================================

/**
 *   
 * MainPage  PC   
 */
type ViewportInfo = {
  /** React Flow transform X  */
  x: number;
  /** React Flow transform Y  */
  y: number;
  /**    (0.3 ~ 2.0) */
  zoom: number;
  /**    */
  width: number;
  /**    */
  height: number;
  /** Flow     X */
  centerX: number;
  /** Flow     Y */
  centerY: number;
};

/**
 * NetworkDiagram  Props
 */
interface NetworkDiagramProps {
  /**    */
  nodes: Node[];
  /**    */
  edges: Edge[];
  /**    ( ) */
  selectedDevice: Device | null;
  /**     */
  onDeviceClick: (device: Device) => void;
  /**       */
  onCanvasClick: () => void;
  /** ()    */
  onEdgeClick: (event: unknown, edge: Edge) => void;
  /**    ( ID ) */
  devices: Device[];
  /**    */
  nodeTypes?: NodeTypes;
  /**    */
  edgeTypes?: EdgeTypes;
  /**   */
  viewMode?: "full" | "smart" | "minimal";
  /**     */
  showOnlyProblems?: boolean;
  /**    */
  zoomLevel?: number;
  /**     */
  keyboardNavigationEnabled?: boolean;
  /** Ping    (UI ) */
  isPinging?: boolean;
  /**     */
  onZoomChange?: (zoomLevel: number) => void;
  /**    ( PC ) */
  onViewportChange?: (info: ViewportInfo) => void;
}

// ==========================================
//   
// ==========================================

/**    -     */
const isLocalDev =
  typeof window !== "undefined" &&
  import.meta.env?.DEV &&
  window.location.hostname === "localhost";

/**    -     */
const INITIAL_ZOOM = 0.6;

// ==========================================
//  
// ==========================================

/**
 *    
 * 
 * React Flow      .
 *   200+    
 *    .
 * 
 *  :
 * -        
 * -    ( )
 * -     
 * -    
 */
const NetworkDiagram = React.memo(function NetworkDiagram({
  nodes,
  edges,
  onDeviceClick,
  onCanvasClick,
  onEdgeClick,
  devices,
  nodeTypes,
  edgeTypes,
  viewMode = "full",
  zoomLevel = 1.0,
  keyboardNavigationEnabled = true,
  isPinging = false,
  onZoomChange,
  onViewportChange,
}: NetworkDiagramProps) {
  
  // ==========================================
  // Refs   
  // ==========================================
  
  /** React Flow  DOM  */
  const reactFlowWrapper = useRef<HTMLDivElement>(null);
  
  /** React Flow   (,  ) */
  const reactFlowInstance = useRef<ReactFlowInstance | null>(null);
  
  /**      (  ) */
  const didFitOnce = useRef(false);
  
  /**     ResizeObserver */
  const resizeObserver = useRef<ResizeObserver | null>(null);
  
  /**     ID */
  const animationFrame = useRef<number | null>(null);
  
  /**   throttling   ID */
  const zoomRaf = useRef<number | null>(null);
  
  /**    (  ) */
  const lastZoom = useRef<number | null>(null);

  // ==========================================
  //   
  // ==========================================
  
  /**     */
  const keyboardControls = useKeyboardNavigation({
    stepSize: 100,           //     (px)
    enabled: keyboardNavigationEnabled && !isPinging, // Ping  
    zoomStep: 1.2,           //   (20% )
    animationDuration: 300,  //   (ms)
  });

  //      
  useEffect(() => {
    if (isLocalDev) console.log("   :", keyboardControls);
  }, [keyboardControls]);

  // ==========================================
  //    
  // ==========================================

  /**
   *     
   *       
   */
  useEffect(() => {
    return () => {
      // ResizeObserver 
      if (resizeObserver.current) {
        resizeObserver.current.disconnect();
        resizeObserver.current = null;
      }

      //     
      if (animationFrame.current) {
        cancelAnimationFrame(animationFrame.current);
        animationFrame.current = null;
      }

      if (zoomRaf.current) {
        cancelAnimationFrame(zoomRaf.current);
        zoomRaf.current = null;
      }

      //    (  )
      if (typeof window !== "undefined" && (window as any).reactFlowInstance) {
        delete (window as any).reactFlowInstance;
      }

      if (isLocalDev) console.log("NetworkDiagram:   ");
    };
  }, []);

  // ==========================================
  //    
  // ==========================================

  /**
   *     
   * 
   *      :
   * - node.data.type "server"  "Server"
   * - devices   "server" 
   * 
   * @param nodeList   
   * @returns    null
   */
  const findServerNode = useCallback(
    (nodeList: Node[]): Node | null => {
      return (
        nodeList.find(
          (node) =>
            node.data?.type === "server" ||
            node.data?.type === "Server" ||
            devices
              .find((d) => d.deviceId.toString() === node.id)
              ?.type.toLowerCase() === "server"
        ) || null
      );
    },
    [devices]
  );

  /**
   *     
   * 
   *         .
   *       .
   * 
   * @param nodeList   
   * @param zoom    (: INITIAL_ZOOM)
   */
  const centerOnServer = useCallback(
    (nodeList: Node[], zoom: number = INITIAL_ZOOM) => {
      const instance = reactFlowInstance.current;
      if (!instance) return;

      const serverNode = findServerNode(nodeList);

      if (serverNode) {
        //     
        if (typeof instance.setCenter === "function") {
          instance.setCenter(serverNode.position.x, serverNode.position.y, {
            zoom,
            duration: 500, // 0.5 
          });
          if (isLocalDev) {
            console.log(
              `[CENTER]   : (${serverNode.position.x}, ${serverNode.position.y}), : ${zoom}`
            );
          }
        }
      } else {
        //      
        if (nodeList.length > 0) {
          const centerX =
            nodeList.reduce((sum, node) => sum + node.position.x, 0) /
            nodeList.length;
          const centerY =
            nodeList.reduce((sum, node) => sum + node.position.y, 0) /
            nodeList.length;

          if (typeof instance.setCenter === "function") {
            instance.setCenter(centerX, centerY, { zoom, duration: 500 });
            if (isLocalDev) {
              console.log(
                `[CENTER]   : (${centerX}, ${centerY}), : ${zoom}`
              );
            }
          }
        }
      }

      //      
      onZoomChange?.(zoom);
    },
    [findServerNode, onZoomChange, isLocalDev]
  );

  // ==========================================
  //  
  // ==========================================

  /**
   *    
   * 
   *  ID  devices     
   *   onDeviceClick  
   * 
   * @param _    
   * @param node   
   */
  const handleNodeClick = useCallback(
    (_: unknown, node: Node) => {
      const device = devices.find((d) => d.deviceId.toString() === node.id);
      if (device) onDeviceClick(device);
    },
    [devices, onDeviceClick]
  );

  /**
   *      
   * 
   *       
   *   
   * 
   * @param event   
   */
  const handlePaneClick = useCallback(
    (event: React.MouseEvent) => {
      if (isLocalDev) console.log(" :", event.clientX, event.clientY);
      onCanvasClick();
    },
    [onCanvasClick]
  );

  /**
   * React Flow   
   * 
   * React Flow    ,
   *       
   * 
   * @param instance React Flow 
   */
  const onInit = useCallback(
    (instance: ReactFlowInstance) => {
      if (isLocalDev) console.log("React Flow  ");
      
      //   
      reactFlowInstance.current = instance;
      
      //      ()
      if (typeof window !== "undefined")
        (window as any).reactFlowInstance = instance;

      // DOM       
      requestAnimationFrame(() => {
        centerOnServer(nodes, INITIAL_ZOOM);
      });
    },
    [centerOnServer, nodes]
  );

  /**
   *     
   * 
   *        .
   *  ,      .
   * 
   * @param node   
   * @returns CSS   (HEX)
   */
  const miniMapNodeColor = useCallback(
    (node: Node) => {
      const device = devices.find((d) => d.deviceId.toString() === node.id);
      
      if (device) {
        // 1:      
        switch (device.status) {
          case "Online":
            return "#00ff00"; //   ()
          case "Offline":
            return "#ff0000"; //   ()
          case "Unstable":
            return "#ffff00"; //   ()
          default:
            return "#cccccc"; //  (  )
        }
      }

      // 2:      ()
      switch (node.data?.type) {
        case "server":
          return "#ffcc00"; //  ()
        case "switch":
          return "#00ccff"; //  ()
        case "pc":
          return "#66ff66"; //   (PC)
        default:
          return "#cccccc"; //  
      }
    },
    [devices]
  );

  /**
   *  /  
   * 
   *  ,   :
   * 1.      
   * 2.      PC   
   * 
   * requestAnimationFrame     
   * 
   * @param _evt    
   * @param viewport   
   */
  const handleMove = useCallback(
    (_evt: unknown, viewport: { x: number; y: number; zoom: number }) => {
      const inst = reactFlowInstance.current;
      const container = reactFlowWrapper.current;
      if (!inst || !container) return;

      //    
      const { width, height } = container.getBoundingClientRect();

      //     Flow  
      //   MainPage  PC   
      const centerInScreen = { x: width / 2, y: height / 2 };
      const centerInFlow = inst.project(centerInScreen);

      //  throttling  requestAnimationFrame 
      if (typeof viewport?.zoom === "number") {
        if (zoomRaf.current) cancelAnimationFrame(zoomRaf.current);
        zoomRaf.current = requestAnimationFrame(() => {
          
          //     onZoomChange  ( )
          if (lastZoom.current !== viewport.zoom) {
            lastZoom.current = viewport.zoom;
            onZoomChange?.(viewport.zoom);
          }
          
          //     
          onViewportChange?.({
            x: viewport.x,
            y: viewport.y,
            zoom: viewport.zoom,
            width,
            height,
            centerX: centerInFlow.x,
            centerY: centerInFlow.y,
          });

          zoomRaf.current = null;
        });
      }
    },
    [onZoomChange, onViewportChange]
  );

  // ==========================================
  // React Flow 
  // ==========================================

  /**
   * React Flow   
   *       
   */
  const reactFlowProps = useMemo(
    () => ({
      //   
      nodesDraggable: false,        //    ( )
      nodesConnectable: false,      //     
      elementsSelectable: true,     // /  
      selectNodesOnDrag: false,     //    
      
      //   
      fitView: false,               //    ( )
      defaultZoom: INITIAL_ZOOM,    //   
      defaultPosition: [0, 0] as [number, number], //  
      
      //     
      minZoom: 0.3,                 //   ( )
      maxZoom: 2,                   //   ( )
      translateExtent: [            //   
        [-3000, -3000],
        [3000, 3000],
      ] as [[number, number], [number, number]],
      
      //  
      onlyRenderVisibleElements: true, //    
    }),
    []
  );

  // ==========================================
  //    Effects
  // ==========================================

  /**
   *       
   * 
   * :
   * 1.    (didFitOnce.current === false)
   * 2. React Flow  
   * 3.   
   * 4. -   (Race condition )
   * 
   * Race condition     source/target
   *       
   */
  useEffect(() => {
    //     
    if (didFitOnce.current) return;
    if (!reactFlowInstance.current) return;
    if (nodes.length === 0) return;

    // -   (Race condition )
    const nodeIds = new Set(nodes.map((n) => n.id));
    const edgesValid = edges.every(
      (e) => nodeIds.has(e.source as string) && nodeIds.has(e.target as string)
    );
    if (!edgesValid) return;

    //    
    if (animationFrame.current) {
      cancelAnimationFrame(animationFrame.current);
    }

    //      
    animationFrame.current = requestAnimationFrame(() => {
      centerOnServer(nodes, INITIAL_ZOOM);
      didFitOnce.current = true;
      animationFrame.current = null;
      
      if (isLocalDev) console.log("[INIT]     ");
    });

    //      
    return () => {
      if (animationFrame.current) {
        cancelAnimationFrame(animationFrame.current);
        animationFrame.current = null;
      }
    };
  }, [nodes, edges, centerOnServer]);

  /**
   *     
   * 
   * ResizeObserver     ,
   *       .
   *    / .
   */
  useEffect(() => {
    const container = reactFlowWrapper.current;
    if (!container || !reactFlowInstance.current) return;
    if (typeof ResizeObserver === "undefined") return;

    //  observer 
    if (resizeObserver.current) {
      resizeObserver.current.disconnect();
    }

    resizeObserver.current = new ResizeObserver(() => {
      //        
      //     
      if (isLocalDev) console.log("[RESIZE]    ");
    });

    resizeObserver.current.observe(container);

    return () => {
      if (resizeObserver.current) {
        resizeObserver.current.disconnect();
        resizeObserver.current = null;
      }
    };
  }, []);

  /**
   *     
   * 
   * Ctrl + Home:    
   *         
   */
  useEffect(() => {
    const handleKeydown = (event: KeyboardEvent) => {
      if (event.ctrlKey && event.key === "Home") {
        event.preventDefault();
        centerOnServer(nodes, INITIAL_ZOOM);
        if (isLocalDev) console.log("[MANUAL]     ");
      }
    };

    window.addEventListener("keydown", handleKeydown);
    return () => window.removeEventListener("keydown", handleKeydown);
  }, [nodes, centerOnServer]);

  // ==========================================
  // 
  // ==========================================

  return (
    <div ref={reactFlowWrapper} style={{ width: "100%", height: "100%" }}>
      <ReactFlow
        nodes={nodes}
        edges={edges}
        nodeTypes={nodeTypes}
        edgeTypes={edgeTypes}
        onNodeClick={handleNodeClick}
        onEdgeClick={onEdgeClick}
        onPaneClick={handlePaneClick}
        onInit={onInit}
        onMove={handleMove}
        onMoveEnd={handleMove}
        {...reactFlowProps}
      >
        {/*  - 5      */}
        <MiniMap
          nodeColor={miniMapNodeColor}
          style={{ display: nodes.length > 5 ? "block" : "none" }}
        />
      </ReactFlow>

      {/*    ( ) */}
      {keyboardNavigationEnabled && !isPinging && (
        <div
          style={{
            position: "absolute",
            bottom: 10,
            left: 10,
            background: "rgba(0,0,0,0.8)",
            color: "white",
            padding: "8px 12px",
            borderRadius: 6,
            fontSize: 11,
            pointerEvents: "none",
            fontFamily: "monospace",
            lineHeight: 1.3,
            zIndex: 1000,
          }}
        >
          <div>:  </div>
          <div>Ctrl + +/-: </div>
          {/* <div>Ctrl + 0: </div> */}
          <div>Ctrl + Home:  </div>
        </div>
      )}

      {/*       */}
      {typeof window !== "undefined" &&
        window.location.hostname === "localhost" && (
          <div
            style={{
              position: "absolute",
              top: 120,
              left: 10,
              background: "rgba(0,0,0,0.8)",
              color: "white",
              padding: "8px 12px",
              borderRadius: 5,
              fontSize: 12,
              pointerEvents: "none",
              fontFamily: "monospace",
              lineHeight: 1.4,
            }}
          >
            <div>
              : {nodes.length} | : {edges.length}
            </div>
            <div>: {zoomLevel.toFixed(2)}x</div>
            <div>
              : {keyboardNavigationEnabled && !isPinging ? "ON" : "OFF"}
            </div>
            <div>: {viewMode}</div>
            <div>: {didFitOnce.current ? "" : ""}</div>
            <div> : {findServerNode(nodes) ? "" : ""}</div>
          </div>
        )}
    </div>
  );
});

NetworkDiagram.displayName = "NetworkDiagram";
export default NetworkDiagram;

/**
 *  
 * 
 * 1.  :
 *    - INITIAL_ZOOM      
 *    - reactFlowProps minZoom/maxZoom   
 *    - onlyRenderVisibleElements    
 * 
 * 2.   :
 *    - handleKeydown     
 *    - useKeyboardNavigation    
 * 
 * 3.  :
 *    - miniMapNodeColor    
 *    -    (: 5  )
 * 
 * 4.    :
 *    - findServerNode    
 *    - centerOnServer   
 * 
 * 5.  :
 *    -      
 *    - React DevTools Profiler  
 * 
 * 6.  :
 *    - 1000+     
 *    -      
 */