import axios, { AxiosError } from "axios";
import type { TraceResponse } from "../types/trace";

/**
 *   Trace  .
 * @param deviceId   ID
 * @param opts     :  (signal), (ms)
 * @returns TraceResponse { path, cables, ... }
 */
export async function fetchTrace(
  deviceId: number,
  opts: { signal?: AbortSignal; timeoutMs?: number } = {}
): Promise<TraceResponse> {
  //       
  if (!Number.isFinite(deviceId)) throw new Error("  deviceId.");

  try {
    // /     
    const res = await axios.get<TraceResponse>(`/api/trace/${deviceId}`, {
      signal: opts.signal,
      timeout: opts.timeoutMs ?? 15000,
    });
    return res.data;
  } catch (err) {
    //    /       
    const ax = err as AxiosError<{ message?: string }>;
    const msg =
      ax.response?.data?.message ??
      (ax.code === "ECONNABORTED" ? "  ." : undefined) ??
      (ax.message?.includes("canceled") ? " ." : undefined) ??
      "Trace   .";
    console.error(" Trace API :", ax);
    throw new Error(msg);
  }
}
