/**
 * useFps.ts -  FPS(Frame Per Second)   
 * 
 * :
 * - React     
 * -        
 * -      
 * 
 *  :
 * - requestAnimationFrame   
 * -    FPS   
 * -    FPS  
 * 
 *  :
 * -  enabled=false  
 * -       
 */

import { useEffect, useRef, useState } from "react";

/**
 * FPS    
 */
interface UseFpsOptions {
  sampleMs?: number;    // FPS   ()
  smooth?: number;      //   (0-1,  )
  enabled?: boolean;    //   
}

/**
 *  FPS   
 * 
 * @param opts  
 * @param opts.sampleMs FPS   (: 500ms)
 * @param opts.smooth   (: 0.2,  )
 * @param opts.enabled    (: true)
 * @returns  FPS  ()
 * 
 * @example
 * ```tsx
 * //  
 * const fps = useFps();
 * 
 * //  
 * const fps = useFps({
 *   sampleMs: 1000,  // 1 
 *   smooth: 0.1,     //   
 *   enabled: process.env.NODE_ENV === 'development'
 * });
 * 
 * return <div>FPS: {fps}</div>;
 * ```
 */
export function useFps(opts: UseFpsOptions = {}) {
  //   
  const { sampleMs = 500, smooth = 0.2, enabled = true } = opts;
  
  /**
   *    ref 
   * useState  useRef  :     
   */
  const frames = useRef(0);                    //      
  const lastTs = useRef(performance.now());    //  FPS  
  const rafId = useRef<number | null>(null);   // requestAnimationFrame ID
  const timerId = useRef<number | null>(null); // setTimeout ID
  
  //   FPS  ( )
  const [fps, setFps] = useState(0);

  useEffect(() => {
    //    
    if (!enabled) return;

    /**
     *   
     * requestAnimationFrame      
     *       
     */
    const loop = () => {
      frames.current++;
      rafId.current = requestAnimationFrame(loop);
    };
    
    //   
    rafId.current = requestAnimationFrame(loop);

    /**
     * FPS    
     * 
     *  :
     * 1.  (dt) 
     * 2.  FPS = (  * 1000) /  
     * 3.    
     * 4.    
     */
    const tick = () => {
      const now = performance.now();
      const dt = now - lastTs.current;
      
      //  FPS  (0  )
      const current = dt > 0 ? (frames.current * 1000) / dt : 0;
      
      /**
       *    FPS 
       * -   (1-smooth)    smooth  
       * - smooth    
       * -        
       */
      setFps(prev => (prev ? prev * (1 - smooth) + current * smooth : current));
      
      //    
      frames.current = 0;
      lastTs.current = now;
      
      //    
      timerId.current = window.setTimeout(tick, sampleMs);
    };
    
    //    
    timerId.current = window.setTimeout(tick, sampleMs);

    /**
     *  
     *         
     *       
     */
    return () => {
      if (rafId.current != null) {
        cancelAnimationFrame(rafId.current);
      }
      if (timerId.current != null) {
        clearTimeout(timerId.current);
      }
    };
  }, [sampleMs, smooth, enabled]); //    

  /**
   * 
   * - enabled=true:  FPS  ( )
   * - enabled=false: 0 (  )
   */
  return enabled ? Math.round(fps) : 0;
}