/**
 * CableForm.tsx -      
 * 
 *  :
 * -        
 * -       
 * -     
 * -      
 * 
 *  :
 * -  :  API UI  
 * -  : useMemo    
 * -  : AbortController alive  
 * -  :   ,   
 * 
 *  :
 * - PC SWITCH  
 * -     
 * -     
 * 
 * :
 * -         
 * -     
 * -       
 */

import { useState, useEffect, useMemo } from "react";
import axios, { AxiosError } from "axios";
import type { Device } from "../types/device";

/**
 *  API    ( API )
 * 
 *      
 *  device   
 */
type ApiPort = {
  portId: number;                    //   ID
  name: string;                      //   (: "GigabitEthernet0/1", "P01")
  device: { deviceId: number };      //   
};

/**
 * UI   
 * 
 *      
 *       
 */
type UiPort = {
  id: number;                        //  ID (ApiPort.portId )
  name: string;                      //   
};

/**
 * CableForm  Props 
 */
interface CableFormProps {
  onSuccess: () => void;             //      
}

/**
 *      
 * 
 *        
 * 300+       
 * 
 * @param props  props
 * @param props.onSuccess      
 * @returns JSX.Element    
 */
export default function CableForm({ onSuccess }: CableFormProps) {
  
  //      
  
  /**
   *    
   *       
   */
  const [cableId, setCableId] = useState("");         //  
  const [description, setDescription] = useState(""); //   ()

  //      
  
  /**
   *    
   *         
   */
  const [devices, setDevices] = useState<Device[]>([]);                           //   
  const [portsByDeviceId, setPortsByDeviceId] = useState<Record<number, UiPort[]>>({}); //   

  //      
  
  /**
   *      
   * 
   *   :
   * - number | "": select   ("")   ID 
   * -    HTML select  
   */
  const [fromDeviceId, setFromDeviceId] = useState<number | "">("");  //   ID
  const [toDeviceId, setToDeviceId] = useState<number | "">("");      //   ID
  const [fromPortId, setFromPortId] = useState<number | "">("");      //   ID
  const [toPortId, setToPortId] = useState<number | "">("");          //   ID

  //  UI/   
  
  /**
   *    
   * ,      
   */
  const [loading, setLoading] = useState(false);       //    
  const [submitting, setSubmitting] = useState(false); //    

  //      
  
  /**
   *  ID     Map 
   * 
   *  :
   * - O(1)     
   * - devices    
   * -       
   */
  const deviceById = useMemo(
    () => new Map<number, Device>(devices.map((d) => [d.deviceId, d])),
    [devices]
  );

  /**
   *     
   * 
   *        
   *       
   */
  const fromPorts = useMemo(
    () => (typeof fromDeviceId === "number" ? portsByDeviceId[fromDeviceId] ?? [] : []),
    [fromDeviceId, portsByDeviceId]
  );
  
  const toPorts = useMemo(
    () => (typeof toDeviceId === "number" ? portsByDeviceId[toDeviceId] ?? [] : []),
    [toDeviceId, portsByDeviceId]
  );

  //     ( ) 
  
  /**
   *        
   * 
   *    :
   * 1. AbortController:     
   * 2. alive :   
   * 3. Promise.all:     
   * 4.   : / 
   */
  useEffect(() => {
    const controller = new AbortController();
    let alive = true;

    (async () => {
      try {
        setLoading(true);
        
        //         
        const [deviceRes, portRes] = await Promise.all([
          axios.get<Device[]>("/api/device", { signal: controller.signal }),
          axios.get<ApiPort[]>("/api/ports", { signal: controller.signal }),
        ]);

        //      
        if (!alive) return;

        setDevices(deviceRes.data);

        /**
         *    
         * 
         * :
         * 1.  ID  
         * 2. ApiPort  UiPort  
         * 3.    
         */
        const map: Record<number, UiPort[]> = {};
        for (const p of portRes.data) {
          const devId = p.device?.deviceId;
          if (typeof devId !== "number") continue; //    ID 
          
          if (!map[devId]) map[devId] = [];
          map[devId].push({ id: p.portId, name: p.name });
        }

        /**
         *     
         * 
         *  :
         * 1.     (: "GigabitEthernet0/5"  5)
         * 2.   1 
         * 3.   2  (   )
         * 
         * : ["P1", "P2", "P10", "P20"] ( : ["P1", "P10", "P2", "P20"])
         */
        for (const k of Object.keys(map)) {
          map[Number(k)].sort((a, b) => {
            const na = parseInt(a.name.replace(/\D+/g, "") || "0", 10);
            const nb = parseInt(b.name.replace(/\D+/g, "") || "0", 10);
            return na - nb || a.name.localeCompare(b.name);
          });
        }

        setPortsByDeviceId(map);
      } catch (error) {
        //       
        if (axios.isCancel(error)) return;
        
        console.error("/  :", error);
        alert("/   .");
      } finally {
        if (alive) setLoading(false);
      }
    })();

    // :   
    return () => {
      alive = false;
      controller.abort();
    };
  }, []);

  //      
  
  /**
   * From        
   * 
   * UX :
   * -       
   * -          
   * -        
   */
  useEffect(() => {
    if (typeof fromDeviceId === "number") {
      const first = portsByDeviceId[fromDeviceId]?.[0]?.id;
      setFromPortId(first ?? "");
    } else {
      setFromPortId("");
    }
  }, [fromDeviceId, portsByDeviceId]);

  /**
   * To        
   * From    
   */
  useEffect(() => {
    if (typeof toDeviceId === "number") {
      const first = portsByDeviceId[toDeviceId]?.[0]?.id;
      setToPortId(first ?? "");
    } else {
      setToPortId("");
    }
  }, [toDeviceId, portsByDeviceId]);

  //      
  
  /**
   *     
   * 
   *  :
   * 1.    
   * 2.    
   * 3.   
   * 
   * @returns     null ( )
   */
  const validate = (): string | null => {
    // 1:   
    if (!cableId.trim()) return " ID .";
    if (typeof fromDeviceId !== "number" || typeof toDeviceId !== "number")
      return "From/To  .";
    if (typeof fromPortId !== "number" || typeof toPortId !== "number")
      return "From/To  .";

    // 2:   
    if (fromDeviceId === toDeviceId) return "     .";

    //      ( )
    if (fromDeviceId === toDeviceId && fromPortId === toPortId)
      return "     .";

    // 3:     
    const from = deviceById.get(fromDeviceId);
    const to = deviceById.get(toDeviceId);
    if (!from || !to) return "    .";

    // 4:     
    const fType = (from.type ?? "").toLowerCase();
    const tType = (to.type ?? "").toLowerCase();

    /**
     *  : PC-SWITCH  
     * 
     *    :
     * - PC  SWITCH 
     * - PC-PC    (  )
     * - PC-SERVER    (   )
     * 
     *    :
     * -      
     * -        
     */
    const invalidPc =
      (fType === "pc" && tType !== "switch") || 
      (tType === "pc" && fType !== "switch");
    if (invalidPc) return "PC SWITCH   .";

    return null; //   
  };

  //     
  
  /**
   *    
   * 
   *  :
   * 1.    
   * 2.   
   * 3. API    
   * 4. /    
   * 5.     
   * 
   * @param e React  
   */
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    //   
    const err = validate();
    if (err) {
      alert(` ${err}`);
      return;
    }

    try {
      setSubmitting(true);

      //   API 
      await axios.post("/api/cable", {
        cableId: cableId.trim(),
        description: description.trim() || undefined, //   undefined 
        fromPortId,
        toPortId,
      });

      alert("  .");
      
      /**
       *    
       *         
       */
      setCableId("");
      setDescription("");
      setFromDeviceId("");
      setToDeviceId("");
      setFromPortId("");
      setToPortId("");

      //     (   )
      onSuccess();
    } catch (err) {
      /**
       *   
       * 
       *     :
       * - 409 Conflict:   
       * -  HTTP :    
       * -  :   
       */
      const error = err as AxiosError<{ message?: string }>;
      const msg =
        error.response?.data?.message ??
        (error.response?.status === 409
          ? "  ."
          : error.message);
      alert(` : ${msg}`);
      console.error("  :", error);
    } finally {
      setSubmitting(false);
    }
  };

  //    
  
  return (
    <div className="bg-white border border-slate-200 shadow-sm rounded-md p-4">

      <form onSubmit={handleSubmit} className="space-y-4" aria-busy={submitting || loading}>
        <h3 className="text-slate-700 font-semibold mb-2">  </h3>

        {/*  ID   */}
        <div>
          <label className="block text-sm mb-1" htmlFor="cable-id"> ID</label>
          <input
            id="cable-id"
            type="text"
            className="input"
            placeholder=": CABLE-001"
            value={cableId}
            onChange={(e) => setCableId(e.target.value)}
            required
            disabled={loading || submitting}
          />
        </div>

        {/*     () */}
        <div>
          <label className="block text-sm mb-1" htmlFor="cable-desc"></label>
          <input
            id="cable-desc"
            type="text"
            className="input"
            placeholder=": PC-01 to SW-01"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            disabled={loading || submitting}
          />
        </div>

        {/* From    */}
        <div>
          <label className="block text-sm mb-1" htmlFor="from-device">From </label>
          <select
            id="from-device"
            className="input"
            value={fromDeviceId}
            onChange={(e) => setFromDeviceId(e.target.value ? Number(e.target.value) : "")}
            required
            disabled={loading || submitting}
          >
            <option value="">--  --</option>
            {devices.map((d) => (
              <option key={d.deviceId} value={d.deviceId}>
                {d.name} ({d.type})
              </option>
            ))}
          </select>
        </div>

        {/* To    */}
        <div>
          <label className="block text-sm mb-1" htmlFor="to-device">To </label>
          <select
            id="to-device"
            className="input"
            value={toDeviceId}
            onChange={(e) => setToDeviceId(e.target.value ? Number(e.target.value) : "")}
            required
            disabled={loading || submitting}
          >
            <option value="">--  --</option>
            {devices.map((d) => (
              <option key={d.deviceId} value={d.deviceId}>
                {d.name} ({d.type})
              </option>
            ))}
          </select>
        </div>

        {/*    */}
        <div className="space-y-4">
          {/* From   */}
          <div>
            <label className="block text-sm mb-1" htmlFor="from-port">From </label>
            <select
              id="from-port"
              className="input"
              value={fromPortId}
              onChange={(e) => setFromPortId(e.target.value ? Number(e.target.value) : "")}
              disabled={loading || submitting || typeof fromDeviceId !== "number"}
            >
              <option value="">--  --</option>
              {fromPorts.map((p) => (
                <option key={p.id} value={p.id}>
                  {p.name}
                </option>
              ))}
            </select>
          </div>

          {/* To   */}
          <div>
            <label className="block text-sm mb-1" htmlFor="to-port">To </label>
            <select
              id="to-port"
              className="input"
              value={toPortId}
              onChange={(e) => setToPortId(e.target.value ? Number(e.target.value) : "")}
              disabled={loading || submitting || typeof toDeviceId !== "number"}
            >
              <option value="">--  --</option>
              {toPorts.map((p) => (
                <option key={p.id} value={p.id}>
                  {p.name}
                </option>
              ))}
            </select>
          </div>
        </div>

        {/*   */}
        <button
          type="submit"
          className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 
                     disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors
                     focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
          disabled={
            loading ||
            submitting ||
            !cableId ||
            typeof fromPortId !== "number" ||
            typeof toPortId !== "number"
          }
        >
          {submitting ? " " : " "}
        </button>
      </form>
    </div>
  );
}