//  client/src/components/CustomNode.tsx

import { memo, useMemo } from "react";
import {
  Monitor,
  Server,
  Router,
  Activity,
  AlertTriangle,
  Wifi,
  WifiOff,
  Ban,
} from "lucide-react";
import { Handle, Position } from "react-flow-renderer";
import type { NodeProps } from "react-flow-renderer";

/**
 * Network Device Custom Node Component
 *
 *  :
 * -      (, , PC, )
 * -  Ping     
 * -     (Dagre, Radial)
 * -  Handle      
 * -     
 *
 *  :
 * -    
 * -     
 * -  Ping 
 * -    
 * -   (React.memo )
 * -     (Tailwind CSS)
 */

// ==========================================
//    
// ==========================================

/**
 *     (Ping  )
 *
 *  Ping        
 *
 * @enum {string}
 */
export type DeviceStatus =
  | "Online"
  | "Offline"
  | "Unstable"
  | "Unknown"
  | "Unreachable";

/**
 *    
 *
 *       
 *
 * @enum {string}
 */
export type DeviceType = "server" | "switch" | "pc" | "router";

/**
 *   
 *
 *     
 *
 * @enum {string}
 */
export type LayoutMode = "radial" | "dagre" | "hierarchical";

/**
 *     (Ping  )
 *
 * React Flow        
 *       
 *
 * @interface CustomNodeData
 */
export interface CustomNodeData {
  /**   -     */
  type: DeviceType;

  /**    -     (Ping  ) */
  status: DeviceStatus;

  /**   -   */
  label: string;

  /**     - Handle   */
  mode?: LayoutMode;

  /**    - UI   */
  showLabel?: boolean;

  /** IP  -     */
  ipAddress?: string;

  /**  Ping  -   () */
  latencyMs?: number | null;

  /**  Ping    */
  lastCheckedAt?: string;

  /**   -    */
  metadata?: Record<string, unknown>;

  /**     - Handle   */
  angleInDegrees?: number;
  highlighted?: boolean;
}

/**
 *    Props 
 *
 * React Flow NodeProps    
 *
 * @interface CustomNodeProps
 * @extends {NodeProps}
 */
interface CustomNodeProps extends NodeProps {
  /**    */
  data: CustomNodeData;

  /**     Handle  */
  sourcePosition?: Position;

  /**     Handle  */
  targetPosition?: Position;
}

// ==========================================
//       (Ping  )
// ==========================================

/**
 *      (Ping  )
 *
 * Tailwind CSS     
 *     
 *
 * @constant
 */
const DEVICE_COLORS = {
  Online: "text-green-500", // :  ()
  Offline: "text-red-500", // :  ()
  Unstable: "text-yellow-500", // :  ()
  Unknown: "text-gray-400", //   :  ()
  Unreachable: "text-red-600", // :   ()
} as const;

/**
 *     
 *
 *      
 *      
 *
 * @constant
 */
const DEVICE_BG_COLORS = {
  Online: "bg-green-50", //   
  Offline: "bg-red-50", //   
  Unstable: "bg-yellow-50", //   
  Unknown: "bg-gray-50", //   
  Unreachable: "bg-red-100", //   
} as const;

/**
 *     
 *
 *       
 *  > / > PC    
 *
 * @constant
 */
const ICON_SIZES = {
  server: 28, // :    ( )
  switch: 24, // :   ( )
  router: 24, // :   ( )
  pc: 20, // PC:   ()
} as const;

/**
 *     
 *
 * Tailwind CSS     
 *       
 *
 * @constant
 */
const NODE_SIZES = {
  server: "w-14 h-14", // 56px  56px
  switch: "w-12 h-12", // 48px  48px
  router: "w-12 h-12", // 48px  48px
  pc: "w-10 h-10", // 40px  40px
} as const;

/**
 * Handle     
 *
 *   Handle    
 *       
 *
 * @constant
 */
const NODE_RADIUS = {
  server: 28, // NODE_SIZES  (w-14 = 56px / 2)
  switch: 24, // NODE_SIZES  (w-12 = 48px / 2)
  router: 24, // NODE_SIZES  (w-12 = 48px / 2)
  pc: 20, // NODE_SIZES  (w-10 = 40px / 2)
} as const;

/**
 * Dagre  Handle 
 *
 *      
 *      
 *
 * @constant
 */
const HANDLE_STYLE = {
  background: "#6b7280", //   (neutral-500)
  border: "2px solid #ffffff", //   ( )
  width: 8, // 8px 
  height: 8,
  borderRadius: "50%", //  
};

/**
 *    Handle 
 *
 *      Handle 
 *     
 *
 * @constant
 */
const RADIAL_HANDLE_STYLE = {
  background: "transparent", //  
  border: "none", //  
  width: 8,
  height: 8,
  pointerEvents: "auto" as const, //    
};

// ==========================================
//    (Ping  )
// ==========================================

/**
 *       
 *
 * @param status -   (Ping  )
 * @returns Tailwind CSS   
 */
const getStatusColor = (status: DeviceStatus): string =>
  DEVICE_COLORS[status] || "text-gray-400";

/**
 *       
 *
 * @param status -   (Ping  )
 * @returns Tailwind CSS   
 */
const getStatusBgColor = (status: DeviceStatus): string =>
  DEVICE_BG_COLORS[status] || "bg-gray-50";

/**
 *      
 *
 * :
 * -     Lucide  
 * -    
 * -   aria-hidden  
 * -     
 *
 * @param type -  
 * @param status -   (Ping  )
 * @returns   React  
 */
const getDeviceIcon = (type: DeviceType, status: DeviceStatus) => {
  const colorClass = getStatusColor(status);
  const size = ICON_SIZES[type] || ICON_SIZES.pc;

  //     props
  const iconProps = {
    size,
    className: colorClass,
    "aria-hidden": true, //    ()
  };

  //    
  switch (type) {
    case "server":
      return <Server {...iconProps} />;

    case "switch":
      return <Router {...iconProps} />;

    case "router":
      return <Wifi {...iconProps} />;

    case "pc":
    default:
      return <Monitor {...iconProps} />;
  }
};

/**
 *        (Ping  )
 *
 *  :
 * - Ping      
 * -     
 * -    
 *
 * @param status -   (Ping )
 * @returns   React 
 */
const getStatusIcon = (status: DeviceStatus) => {
  const props = { size: 12, className: getStatusColor(status) };

  switch (status) {
    case "Online":
      //  : Activity  ( )
      return <Activity {...props} />;

    case "Unstable":
      //  :  
      return <AlertTriangle {...props} />;

    case "Offline":
      //  : WiFi  
      return <WifiOff {...props} />;

    case "Unreachable":
      //  :  
      return <Ban {...props} />;

    case "Unknown":
    default:
      //   :   
      return <div className="w-3 h-3 rounded-full bg-gray-400" />;
  }
};

/**
 *       
 *
 * @param latencyMs -  ()
 * @returns Tailwind CSS   
 */
const getLatencyColor = (latencyMs: number | null): string => {
  if (latencyMs === null) return "text-gray-500";
  if (latencyMs < 100) return "text-green-600"; // 
  if (latencyMs < 500) return "text-yellow-600"; // 
  return "text-red-600"; // 
};

/**
 *     
 *
 * @param latencyMs -  ()
 * @returns   
 */
const getLatencyText = (latencyMs: number | null): string => {
  if (latencyMs === null) return "timeout";
  return `${latencyMs}ms`;
};

/**
 *       
 *
 *  :
 * -     
 * -     
 * -     
 * -     
 *
 *   :
 * -  :   +  
 * -  :   +  
 * -    UX
 *
 * @param selected -   
 * @param status -  
 * @param type -  
 * @returns   
 */

//      
const LABEL_STYLES = {
  server:
    "mt-2 text-xs font-black text-white bg-blue-900/90 backdrop-blur-sm px-3 py-1.5 rounded-lg text-center max-w-24 truncate shadow-lg border border-white/20",
  switch:
    "mt-2 text-xs font-bold text-white bg-purple-900/90 backdrop-blur-sm px-2 py-1 rounded-md text-center max-w-20 truncate shadow-md border border-white/20",
  router:
    "mt-2 text-xs font-bold text-white bg-green-900/90 backdrop-blur-sm px-2 py-1 rounded-md text-center max-w-20 truncate shadow-md border border-white/20",
  pc: "mt-2 text-xs text-gray-700 font-medium text-center max-w-20 truncate",
} as const;

const getNodeStyles = (
  selected: boolean,
  highlighted: boolean,
  status: DeviceStatus,
  type: DeviceType
) => {
  //   :  >  > 
  const ring = selected
    ? "ring-2 ring-amber-400 ring-offset-2" //  :  
    : highlighted
    ? "ring-8 ring-red-400 ring-offset-2 animate-pulse" //  : 
    : "ring-1 ring-slate-200"; // :   

  //     
  const pulse = highlighted ? "animate-pulse" : "";

  //       
  const hoverEffect = "hover:ring-2 hover:ring-blue-300 hover:scale-105";

  //    (200ms)
  const transition = "transition-all duration-200 ease-in-out";

  //  : /    
  const shadow = selected
    ? "drop-shadow-[0_0_3px_white]" // :  
    : highlighted
    ? "drop-shadow-[0_0_6px_pink]" // :  
    : "drop-shadow-[0_0_2px_gray]"; // :  

  //    (Online=, Offline= )
  const bgColor = getStatusBgColor(status);

  //     (server > switch > pc)
  const nodeSize = NODE_SIZES[type] || NODE_SIZES.pc;

  return {
    //   :  +  +  + 
    container: `${nodeSize} rounded-full ${bgColor} border-2 border-white ${ring} ${pulse} ${shadow} ${hoverEffect} ${transition} flex items-center justify-center cursor-pointer relative`,

    //    (  )
    label: LABEL_STYLES[type] || LABEL_STYLES.pc,

    //  :    (  )
    statusBadge:
      "absolute -top-1 -right-1 w-4 h-4 rounded-full bg-white flex items-center justify-center shadow-sm",
  };
};

/**
 *   Handle    
 *
 *   :
 * -    Handle  
 * -      
 * - CSS transform   
 *
 * @param position - Handle  (Top, Bottom, Left, Right)
 * @param nodeType -   ( )
 * @returns CSS   (   )
 */
function getRadialHandleOffset(
  position: Position,
  nodeType: DeviceType
): {
  top?: string;
  bottom?: string;
  left?: string;
  right?: string;
  transform?: string;
} {
  //    
  const radius = NODE_RADIUS[nodeType] || NODE_RADIUS.pc;

  switch (position) {
    case Position.Top:
      //   Handle:     
      return {
        top: `-${radius}px`,
        left: "50%",
        transform: "translateX(-50%)",
      };

    case Position.Bottom:
      //  Handle:     
      return {
        bottom: `-${radius}px`,
        left: "50%",
        transform: "translateX(-50%)",
      };

    case Position.Left:
      //  Handle:     
      return {
        left: `-${radius}px`,
        top: "50%",
        transform: "translateY(-50%)",
      };

    case Position.Right:
      //  Handle:     
      return {
        right: `-${radius}px`,
        top: "50%",
        transform: "translateY(-50%)",
      };

    default:
      // :   
      return {};
  }
}

// ==========================================
//    (Ping   )
// ==========================================

/**
 *     (Ping   )
 *
 *   :
 * - React.memo   
 * - useMemo    
 * -    (ARIA )
 * -   
 *
 *   :
 * -     (Dagre/Radial)
 * -  Handle  
 * -  Ping  
 * -   
 * -   
 *
 *   :
 * - Ping   
 * -   
 * -   
 * -   
 */
function CustomNode({
  data,
  selected = false,
  targetPosition = Position.Top,
}: CustomNodeProps) {
  //    
  const showLabel = data.showLabel ?? true;
  const mode = data.mode || "dagre";
  const type = data.type;
  const status = data.status;

  //     ( )
  const styles = useMemo(
    () => getNodeStyles(selected, data.highlighted === true, status, type),
    [selected, data.highlighted, status, type]
  );

  //    
  const deviceIcon = useMemo(() => getDeviceIcon(type, status), [type, status]);
  const statusIcon = useMemo(() => getStatusIcon(status), [status]);

  //    Handle  
  const dagreHandleStyle =
    mode === "dagre" ? HANDLE_STYLE : RADIAL_HANDLE_STYLE;

  //    Handle  
  const targetOffset =
    mode === "radial" ? getRadialHandleOffset(targetPosition, type) : {};

  return (
    <div
      className="flex flex-col items-center relative z-10"
      role="button"
      tabIndex={0}
      aria-label={`${type} ${data.label} - ${status} ${
        data.latencyMs ? `(${data.latencyMs}ms)` : ""
      }`}
      aria-selected={selected}
    >
      {/*  Target Handle -   */}
      <Handle
        type="target"
        position={targetPosition}
        id="target"
        style={{
          ...dagreHandleStyle,
          ...(mode === "radial" && targetOffset),
        }}
      />

      {/*  Central Handle -   ( ) */}
      <Handle
        type="source"
        position={Position.Bottom}
        id="center-handle"
        style={{
          position: "absolute",
          left: "50%",
          top: "50%",
          transform: "translate(-50%, -50%)",
          ...RADIAL_HANDLE_STYLE,
        }}
      />

      {/*  Server Node Special Handles (  ) */}
      {mode === "radial" && type === "server" && (
        <>
          {[0, 45, 90, 135, 180, 225, 270, 315].map((angle) => {
            const rad = (angle * Math.PI) / 180;
            const offset = 28;
            const isSource = angle < 180;

            return (
              <Handle
                key={`server-${angle}`}
                type={isSource ? "source" : "target"}
                position={Position.Bottom}
                id={`${isSource ? "source" : "target"}-${angle}`}
                style={{
                  position: "absolute",
                  left: `${50 + Math.cos(rad) * offset}px`,
                  top: `${50 + Math.sin(rad) * offset}px`,
                  transform: "translate(-50%, -50%)",
                  ...RADIAL_HANDLE_STYLE,
                }}
              />
            );
          })}
        </>
      )}

      {/*    */}
      <div className={styles.container}>
        {/*    */}
        {deviceIcon}

        {/*    () - Ping   */}
        <div className={styles.statusBadge}>{statusIcon}</div>
      </div>

      {/*    */}
      {showLabel && (
        <div className={styles.label} title={data.label}>
          {data.label}
        </div>
      )}

      {/* IP   () */}
      {showLabel && data.ipAddress && (
        <div className="text-xs text-gray-500 font-mono mt-1">
          {data.ipAddress}
        </div>
      )}

      {/* Ping   */}
      {showLabel && data.latencyMs !== undefined && (
        <div
          className={`text-xs font-semibold mt-1 ${getLatencyColor(
            data.latencyMs
          )}`}
          title={`Ping: ${getLatencyText(data.latencyMs)} ${
            data.lastCheckedAt
              ? `(${new Date(data.lastCheckedAt).toLocaleTimeString()})`
              : ""
          }`}
        >
           {getLatencyText(data.latencyMs)}
        </div>
      )}
    </div>
  );
}

//  : React.memo   
export default memo(CustomNode);
